<template>
  <div class="vk-app-wrapper">
    <div class="vk-app-form">
      <div class="input-field">
        <label>
          Vk APP Id

          <input type="text" v-model="vk.appId" />
        </label>
      </div>

      <div class="form-controls">
        <button @click="requestToken">
          fetch token
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VkAppComponnet',

  props: {
    vk: {
      type: Object,
      required: true
    }
  },

  methods: {
    getVkAuthLink(appId) {
      return [
        'https://oauth.vk.com/authorize?' + `client_id=${appId}`,
        'response_type=token'
      ].join('&');
    },

    requestToken() {
      const url = this.getVkAuthLink(this.vk.appId);

      const wnd = window.open(url);

      // alert(url);
    }
  }
};
</script>
