<template>
  <div id="app">
    <vk-app-component v-bind:vk="vk" />

    <hr>
    <input
      type="text"
      placeholder="Token"
      v-model="token" />
    <hr>

    <h3>Post list</h3>
    <ul>
      <li>https://vk.com/wall-52045040_333606</li>
      <li></li>
    </ul>

    <div class="request-form-wrapper">
      <div class="form-field">
        <input type="text" placeholder="Insert post id" v-model="requestLink" />
      </div>
      <div class="form-controls">
        <button
          v-on:click="getPostInfo"
          v-bind:disabled="!requestLink"
        >Fetch post</button>
      </div>
    </div>

    <div class="results-wrapper">
      <ul v-if="results.length > 0">
        <li v-for="result in results" v-bind:key="result.src">
          <span>{{ result.src }}</span>
        </li>
      </ul>
    </div>

    <hr>
    <h3>Album photos</h3>

    <ul>
      <li>https://vk.com/album-52045040_236407624</li>
      <li></li>
    </ul>

    <div class="request-form-wrapper">
      <div class="form-field">
        <input type="text" placeholder="Insert album id" v-model="requestAlbumLink" />
      </div>
      <div class="form-controls">
        <button
          v-on:click="getAlbumInfo"
          v-bind:disabled="!requestAlbumLink"
        >Fetch album</button>
      </div>
    </div>

    <div class="results-wrapper">
      <ul v-if="albumResults.length > 0">
        <li v-for="(result, idx) in albumResults" v-bind:key="idx">
          <span>{{ result }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
import jsonp from 'jsonp';
import VkAppComponent from './components/VkApp.vue';

const VK_TOKEN = 'vk-token';

export default {
  name: 'app',

  components: {
    VkAppComponent
  },

  data() {
    return {
      vk: {
        appId: '4179455',

      },

      token: '',
      requestLink: '',

      requestAlbumLink: '',

      results: [],
      albumResults: [],
    }
  },

  created() {
    const token = localStorage.getItem(VK_TOKEN);

    if (token) {
      this.token = token;

      this.checkIfTokenValid();
    }
  },

  methods: {
    checkIfTokenValid() {
      console.warn('skip check token validity');
    },

    getPostId(link) {
      const matches = link.match(/wall(-?\d+_\d+)/);

      if (!matches) return;

      return matches[1];
    },

    getAlbumId(link) {
      const matches = link.match(/album(-?\d+_\d+)/);

      if (!matches) return;

      return matches[1];
    },

    getPostInfo() {
      const postId = this.getPostId(this.requestLink);

      if (!postId) {
        alert('Failed to parse post id');
        return;
      }

      const url = [
        'https://api.vk.com/method/wall.getById?' + `posts=${postId}`,
        `access_token=${this.token}`,
        'v=5.50'
      ].join('&');

      jsonp(url, null, (err, data) => {
        if (err) {
          console.warn('Failed to get post data,', err);
          return;
        }

        const postInfo = data.response[0];
        this.results = this.parsePhotos(postInfo);
      });

      // axios.get(url)
      //   .then((resp) => {
      //     // get photo attachments
      //     const postInfo = resp.data.response[0];
      //     this.results = this.parsePhotos(postInfo);
      //   })
      //   .catch(err => console.error(err));
    },

    getAlbumInfo() {
      const albumFullId = this.getAlbumId(this.requestAlbumLink);

      if (!albumFullId) {
        alert('Failed to parse album id');
        return;
      }

      const [ownerId, albumId] = albumFullId.split('_');

      const url = [
        'https://api.vk.com/method/photos.get?' + `owner_id=${ownerId}&album_id=${albumId}`,
        `access_token=${this.token}`,
        'v=5.50'
      ].join('&');


      jsonp(url, null, (err, data) => {
        if (err) {
          console.warn('Failed to get album data,', err);
          return;
        }

        const albumInfo = data.response.items;
        this.albumResults = this.parseAlbumPhotos(albumInfo);
      });
    },

    parseAlbumPhotos(album) {
      return album.map((item) => {
        const photoSizes = Object.keys(item)
          .filter(x => /^photo/.test(x))
          .map(x => x.split('_')[1])
          .map(x => parseInt(x, 10));

        const maxSize = Math.max.apply(null, photoSizes);

        return item[`photo_${maxSize}`];
      });
    },

    parsePhotos(post) {
      if (!post || !post.attachments) return [];
      return post.attachments
        .filter(a => a.type === 'photo')
        .map((item) => {
          const keys = Object.keys(item.photo)
            .filter(k => /^photo/.test(k));

          return {
            thumb: item.photo[ keys[1] ],
            src: item.photo[ keys.pop() ],
          };
        });
    },
  },

  watch: {
    token(newValue) {
      localStorage.setItem(VK_TOKEN, newValue);
    }
  }
};
</script>

